.button {
  font-family: inherit;
  font-weight: 400;
  margin: 0.5rem 0.1rem 0 0.1rem;
  background-color: #fa787c;
  border: 0;
  border-radius: 5px;
  padding: 0.5rem;
  cursor: pointer;
}

.button:hover {
  background-color: #fe4c52;
}

@media screen and (max-width: 820px) {
  .button {
    font-size: 20px;
    margin: 0.5rem;
  }
}
